// src/components/Home.js

import React,{ useState } from 'react';
import { Container,Row,Col,Image,Card,Form,Button } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import licenseImage from './photo/license_image.jpg';
import taxiImage from './photo/taxi_image.jpg';

const Home = () => {
  const [plateNumber,setPlateNumber] = useState('');
  const [licenseNumber,setLicenseNumber] = useState('');
  const navigate = useNavigate();

  const handleVehicleData = () => {
    if (plateNumber) {
      navigate('/vehicle-status/' + plateNumber);
    }
  };

  const handleLicenseNumber = () => {
    if (licenseNumber) {
      navigate('/driver-info/' + licenseNumber);
    }
  };

  return (
    <Container fluid>
      <Card className="text-center mb-4">
        <Card.Body>
          <Card.Title>Check Your TLC License Status and FHV Information</Card.Title>
          <Card.Text>
            Discover comprehensive information about NYC's taxi and limousine services.
            Here, you'll find resources, images, and updates related to the commission.
          </Card.Text>
        </Card.Body>
      </Card>

      <Row className="text-center">
        <Col md={6} className="mb-4 d-flex flex-column align-items-center">
          <Image
            src={taxiImage}
            alt="NYC Taxi"
            fluid
            rounded
            style={{ height: '300px',objectFit: 'cover',marginBottom: '1rem' }}
          />
          <h3>NYC Yellow Taxi</h3>
          <Form className="mt-3" style={{ width: '100%' }} onSubmit={handleVehicleData}>
            <Form.Group controlId="taxiLicenseInput">
              <Form.Control
                type="text"
                required
                placeholder="Enter Vehicle Plate Number"
                value={plateNumber}
                onChange={(e) => setPlateNumber(e.target.value)}
              />
            </Form.Group>
            <Button type="submit" variant="primary" className="mt-2" >Check Status</Button>
          </Form>
        </Col>

        <Col md={6} className="mb-4 d-flex flex-column align-items-end">
          <Image
            src={licenseImage}
            alt="NYC Limousine"
            fluid
            rounded
            style={{ height: '300px',objectFit: 'cover',marginBottom: '1rem',marginLeft: '-10px' }}
          />
          <h3>NYC TLC License</h3>
          <Form className="mt-3" style={{ width: '100%' }} onSubmit={handleLicenseNumber}>
            <Form.Group controlId="limousineLicenseInput">
              <Form.Control
                type="text"
                required
                placeholder="Enter Taxi License Number"
                value={licenseNumber}
                onChange={(e) => setLicenseNumber(e.target.value)}
              />
            </Form.Group>
            <Button type="submit" variant="primary" className="mt-2" >Get Info</Button>
          </Form>
        </Col>
      </Row>
    </Container>
  );
};

export default Home;
