import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import React from 'react';
import { Container,Image } from 'react-bootstrap';
import Home from './component/Home';
import { Route,Routes } from 'react-router-dom';
import DriverInfo from './component/DriverInfo';
import nycLogo from './component/photo/nyc_taxi.png';
import VehicleInfo from './component/VehicleInfo';

function App() {
  return (
    <div style={{
      display: 'flex',
      flexDirection: 'column',
      minHeight: '100vh'
    }}>
      <Container fluid className="p-5" style={{ flex: 1 }}>

        <Routes>

          <Route path="/" element={<Home />} />
          <Route path="/driver-info/:licenseNumber" element={<DriverInfo />} />
          <Route path="/vehicle-status/:plateNumber" element={<VehicleInfo />} />
        </Routes>
      </Container>

      <footer className="text-center p-3" style={{ backgroundColor: '#f8f9fa',width: '100%' }}>
        <Image
          src={nycLogo}
          alt="NYC Taxi and Limousine Commission Logo"
          fluid
          style={{ maxHeight: '100px' }}
        />
        <p className="mt-2">&copy; {new Date().getFullYear()} NYC Taxi and Limousine Commission. All Rights Reserved.</p>
      </footer>
    </div>
  );
}

export default App;
