// src/components/VehicleInfo.js
import React,{ useEffect,useState } from 'react';
import { useParams } from 'react-router-dom';
import TlcApi from './service/TlcApi';  // Import the API for fetching data
import { Card,Spinner,Alert } from 'react-bootstrap';

const VehicleInfo = () => {
    const { plateNumber } = useParams(); // Get plateNumber from the URL params
    const [vehicleData,setVehicleData] = useState(null);
    const [error,setError] = useState('');
    const [loading,setLoading] = useState(true); // Set loading to true initially

    useEffect(() => {
        const fetchVehicleData = async () => {
            try {
                const data = await TlcApi.fetchVehicleData(plateNumber); // Fetch data using plateNumber
                console.log('Fetched Vehicle Data:',data);
                setTimeout(() => {
                    setVehicleData(data);
                    setLoading(false);
                },1000); // Log the response to debug
            } catch (err) {
                console.error('Error fetching vehicle data:',err); // Log the error
                setError('Error fetching data. Please try again.');
                setLoading(false); // Ensure loading is false if there's an error
            }
        };

        if (plateNumber) {
            fetchVehicleData();
        }
    },[plateNumber]);

    return (
        <div style={{ padding: '20px' }}>
            <h1>Vehicle Information</h1>
            {loading ? (
                <div className="text-center">
                    <Spinner animation="border" role="status" variant="primary" style={{ width: '3rem',height: '3rem' }}>
                        <span className="visually-hidden">Loading...</span>
                    </Spinner>
                    <h2 className="mt-3">Loading information for Plate Number: <strong>{plateNumber}</strong></h2>
                </div>
            ) : error ? (
                <Alert variant="danger">{error}</Alert>
            ) : !vehicleData || Object.keys(vehicleData).length === 0 ? (
                <Alert variant="warning">
                    No vehicle information found for plate number: <strong>{plateNumber}</strong>
                </Alert>
            ) : (
                <Card style={{ padding: '20px' }}>
                    <Card.Title>Vehicle Details</Card.Title>
                    <Card.Body>
                        <ul>
                            <li><strong>Active:</strong> {vehicleData.active}</li>
                            <li><strong>Vehicle License Number:</strong> {vehicleData.vehicle_license_number}</li>
                            <li><strong>Name:</strong> {vehicleData.name}</li>
                            <li><strong>License Type:</strong> {vehicleData.license_type}</li>
                            <li><strong>Expiration Date:</strong> {vehicleData.expiration_date}</li>
                            <li><strong>Permit License Number:</strong> {vehicleData.permit_license_number}</li>
                            <li><strong>DMV License Plate Number:</strong> {vehicleData.dmv_license_plate_number}</li>
                            <li><strong>Vehicle VIN Number:</strong> {vehicleData.vehicle_vin_number}</li>
                            <li><strong>Wheelchair Accessible:</strong> {vehicleData.wheelchair_accessible}</li>
                            <li><strong>Vehicle Year:</strong> {vehicleData.vehicle_year}</li>
                            <li><strong>Base Number:</strong> {vehicleData.base_number}</li>
                            <li><strong>Base Name:</strong> {vehicleData.base_name}</li>
                            <li><strong>Base Type:</strong> {vehicleData.base_type}</li>
                            <li><strong>Veh:</strong> {vehicleData.veh}</li>
                            <li><strong>Base Telephone Number:</strong> {vehicleData.base_telephone_number}</li>
                            <li><strong>Base Address:</strong> {vehicleData.base_address}</li>
                            <li><strong>Reason:</strong> {vehicleData.reason}</li>
                            <li><strong>Last Date Updated:</strong> {vehicleData.last_date_updated}</li>
                            <li><strong>Last Time Updated:</strong> {vehicleData.last_time_updated}</li>
                        </ul>
                    </Card.Body>
                </Card>
            )}
        </div>
    );
};

export default VehicleInfo;
