import axios from 'axios';

const API_BASE_URL = process.env.REACT_APP_API_BASE_URL;

class TlcApi {
    // Fetch driver data by license number
    static async fetchDriverData(licenseNumber) {
        try {
            const response = await axios.get(API_BASE_URL + 'license-status/' + licenseNumber);
            return response.data; // Returns the DriverResponse data
        } catch (error) {
            console.error('Error fetching driver data:', error);
            throw error; // Rethrow the error to handle it in the component
        }
    }

    // Fetch vehicle data by plate number
    static async fetchVehicleData(plateNumber) {
        try {
            const response = await axios.get(API_BASE_URL + 'plate-number/' + plateNumber);
            return response.data; // Returns the VehicleInfoResponse data
        } catch (error) {
            console.error('Error fetching vehicle data:', error);
            throw error; // Rethrow the error to handle it in the component
        }
    }
}

export default TlcApi;
