// src/components/DriverInfo.js

import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import TlcApi from './service/TlcApi';
import { Card, Spinner, Alert } from 'react-bootstrap';

const DriverInfo = () => {
    const { licenseNumber } = useParams(); // Get the licenseNumber from the URL
    const [driverData, setDriverData] = useState(null);
    const [error, setError] = useState('');
    const [loading, setLoading] = useState(true); // Set loading to true initially

    useEffect(() => {
        const fetchDriverData = async () => {
            try {
                const data = await TlcApi.fetchDriverData(licenseNumber);
                // Simulate a delay of 1 second for loading effect
                setTimeout(() => {
                    setDriverData(data);
                    setLoading(false); // Set loading to false after data is fetched
                }, 1000);
            } catch (err) {
                setError('Error fetching data. Please try again.');
                setLoading(false); // Ensure loading is false if there's an error
            }
        };

        if (licenseNumber) {
            fetchDriverData();
        }
    }, [licenseNumber]);

    return (
        <div style={{ padding: '20px' }}>
            <h1>Driver Information</h1>
            {loading ? (
                <div className="text-center">
                    <Spinner animation="border" role="status" variant="primary" style={{ width: '3rem', height: '3rem' }}>
                        <span className="visually-hidden">Loading...</span>
                    </Spinner>
                    <h2 className="mt-3">Loading information for TLC License Number: <strong>{licenseNumber}</strong></h2>
                </div>
            ) : error ? (
                <Alert variant="danger">{error}</Alert>
            ) : !driverData || Object.keys(driverData).length === 0 ? (
                // Updated the error message here to reflect "No driver information found" for the license number
                <Alert variant="warning">
                    No driver information found for license number: <strong>{licenseNumber}</strong>
                </Alert>
            ) : (
                <Card style={{ padding: '20px' }}>
                    <Card.Title>Driver Details</Card.Title>
                    <Card.Body>
                        <ul>
                            <li><strong>Name:</strong> {driverData.name}</li>
                            <li><strong>License Number:</strong> {driverData.license_number}</li>
                            <li><strong>Type:</strong> {driverData.type}</li>
                            <li><strong>Expiration Date:</strong> {driverData.expiration_date}</li>
                            <li><strong>Wheelchair Accessible Trained:</strong> {driverData.wheelchair_accessible_trained}</li>
                            <li><strong>Last Date Updated:</strong> {driverData.last_date_updated}</li>
                            <li><strong>Last Time Updated:</strong> {driverData.last_time_updated}</li>
                        </ul>
                    </Card.Body>
                </Card>
            )}
        </div>
    );
};

export default DriverInfo;
